import { likeThisFeed, unlikeThisFeed } from '@/config/api.js'

const likeMixin = {
	methods: {
		async clickLike (e) {
			// 
			if (!this.token) {
				uni.showToast({
					icon: 'error',
					title: '',
					duration: 1000
				})
				const result = await this.$showLogin(this)
				return
			}
			// 
			if (e.has_like) {
				// 
				try {
					await unlikeThisFeed({
						id: e.id
					})
				} catch (error) {
					return uni.showToast({
						title: '',
						icon: 'error'
					})
				}
			} else {
				// 
				try {
					await likeThisFeed({
						id: e.id
					})
				} catch (error) {
					return uni.showToast({
						title: '',
						icon: 'error'
					})
				}
			}
			uni.showToast({
				title: e.has_like ? '' : '',
				icon: 'success'
			})
			e.has_like = !e.has_like
		},
	}
}
export default likeMixin